import cx from "classnames";
import FireImg from "../../assets/images/common/fire.png";
import DownArrowImg from "../../assets/images/common/down-arrow.png";
import HumansCardsImg from "../../assets/images/common/humans-cards.png";

const DashboardCard = (props) => {
  const { lohPoint } = props;
  return (
    <div className={cx("dashboard-card")}>
      <h6>Your dashboard</h6>
      <div
        className={cx(
          "d-flex",
          "justify-between",
          "align-center",
          "gap-1",
          "px-1"
        )}
      >
        <div className={cx("img-container")}>
          <img src={FireImg} alt="fire" width="100%" />
        </div>
        <p className={cx("loh-points")}>{lohPoint}</p>
        <p>
          loh <br /> points
        </p>
      </div>
      <div className={cx("d-flex", "justify-center")}>
        <img src={DownArrowImg} alt="down-arrow" width="30%" />
      </div>
      <div
        className={cx(
          "d-flex",
          "justify-between",
          "align-center",
          "gap-1",
          "px-1"
        )}
      >
        <div className={cx("img-container")}>
          <img src={HumansCardsImg} alt="humans-cards" width="100%" />
        </div>
        <p className={cx("loh-points")}>{parseInt(lohPoint / 100)}</p>
        <p>
          loh <br /> humans
        </p>
      </div>
    </div>
  );
};

export default DashboardCard;
